  "</p><p>„Soţiilor, fiţi supuse soţilor voştri, ca Domnului, căci soţul este capul soţiei, după cum şi Hristos este capul Bisericii, El, Mântuitorul trupului. Şi după cum Biserica este supusă lui Hristos, tot aşa şi soţiile să fie supuse soţilor lor în toate. Soţilor, iubiţi-vă soţiile, după cum şi Hristos a iubit Biserica şi S-a dat pe Sine pentru ea, ca s-o sfinţească, curăţind-o prin spălarea cu apă prin Cuvânt, ca să înfăţişeze înaintea Sa această Biserică slăvită, fără pată, fără încreţitură sau altceva de felul acesta, ci sfântă şi fără defect. Tot aşa sunt datori şi bărbaţii să-şi iubească soţiile, ca pe trupurile lor. Cine-şi iubeşte soţia se iubeşte pe sine însuşi. Căci nimeni nu şi-a urât vreodată trupul său, ci-l hrăneşte şi-l îngrijeşte cu drag, ca şi Hristos Biserica; pentru că noi suntem mădulare ale trupului Său. De aceea va lăsa omul pe tatăl său şi pe mama sa şi se va lipi de soţia sa şi cei doi vor fi un singur trup. Taina aceasta este mare; dar vorbesc despre Hristos şi despre Biserică. Încolo, fiecare din voi să-şi iubească soţia ca pe sine şi soţia să se teamă de soţ”. Efes. 5:22-33.</p></div>",

  "<div><p>Faptul că unirile tipice sunt în general atât de imperfecte şi nesatisfăcătoare nu anulează ideea că s-a intenţionat ca mariajul să fie un tip, întocmai cum şi multe din sacrificiile israeliţilor au fost imperfecte şi nesatisfăcătoare, dar cu toate acestea constituie tipuri ale sacrificiului adevărat. Noua Creaţie trebuie să aprecieze cu mult mai mult căsătoria tipică, pământească şi datoriile şi responsabilităţile ei cuvenite, datorită aprecierii ei faţă de unirea antitipică între Cristos şi Biserica Sa. Considerat astfel, fiecare bărbat creştin găseşte cea mai mare exemplificare posibilă a datoriilor şi responsabilităţilor sale faţă de soţia sa în grija Domnului faţă de Biserică şi faţă de fiecare interes al ei, pământesc, spiritual, prezent şi viitor, până la sacrificarea vieţii Sale pentru ea. La fel soţia, apreciind datoriile şi responsabilităţile Bisericii faţă de Domnul, discerne un ideal mai înalt al datoriei şi relaţiei soţiei cu soţul ei, ca ajutor al lui. Dar nu trebuie să aşteptăm ca aceste relaţii deosebite şi aplicarea lor cuvenită să poată fi discernute clar decât de către cei care au mintea lui Cristos. Ca atare, deşi îndemnăm pe toţi care contractează legătura căsătoriei să realizeze cât pot de mult idealul divin, notăm totuşi că nimeni nu poate înţelege, aprecia şi aplica toate principiile şi idealurile legate de acest tip, decât cei care au fost concepuţi de Spirit -- Creaţia Nouă -- fiindcă numai aceştia au mintea lui Cristos.</p></div>",
